<template>
    <div class="articleList">
        <ul>
            <router-link 
            :to="'/article/' + (article)"
            v-for="article in this.articleList"> 
                <SingleArticle 
                :articleID="article -1"
                />
            </router-link>
        </ul>
    </div>
</template>

<script>
import SingleArticle from "./SingleArticle.vue"
export default {
    name: "ArticleList",
    components: {
        SingleArticle
    },
    data() {
        return {
        articleList : this.$store.state.articles.map(({id}) => {
                return id
            }).reverse().slice(3)
        }
    },
    methods: {
    }
};

</script>

<style scoped lang="scss">
@import "../../../styles/globalstyles.scss";

@include newsTextSettings;

.articleList{
    @include innerContainer;
    @include roundCorner;
    background-color: $humanorange;

    ul{
        @include columnFlex;
        @include padding10px;
        height:100%;width:100%;
        gap:10px;
        a{
            text-decoration: none;
        }
    }
}
</style>