<template>
    <div class="news">
        <router-link :to="'/article/' + (articles[0]+1)" class="bigNews">
            <div class="previewImage">
                <div 
                :style="{backgroundImage: `url(${this.$store.state.articles[articles[0]].pictures}1.jpg)`}"
                class="actualImage">
                    <!-- IMG PLACEHOLDER -->
                </div>
            </div>
            <div class="newsInfo">
                <div class="newsTitle">
                    {{ this.$store.state.articles[articles[0]].name }}
                </div>
                <div class="newsAbout">
                    {{ this.$store.state.articles[articles[0]].description }}
                </div>
            </div>
        </router-link>
        <div class="sideNews">
            <SideNews :articleID="this.articles[1]" />
            <SideNews :articleID="this.articles[2]" />
        </div>
    </div>
</template>

<script>
// @ is an alias to /src
import SideNews from "./SideNews.vue";
export default {
    name: "MainNews",
    components: {
        SideNews,
    },
    data() {
        return {
            articles: this.$store.state.articles
                .map(({ id }) => {
                    return id - 1;
                })
                .reverse(),
        };
    },
    methods: {
    },
};
</script>

<style scoped lang="scss">
@import "../../../styles/globalstyles.scss";

@include newsTextSettings;
.news {
    @include innerContainer;
    @include padding10px;
    @include roundCorner;

    height: 50vh;
    background-color: $humanorange;
    gap: 10px;
    #main {
        width: 80%;
        height: 100%;
    }
    .bigNews {
        @include padding10px;
        @include roundCorner;
        height: 100%;
        width: 100%;
        background-color: $beerbrown;
        text-decoration: none;

        display: flex;
        .previewImage {
            @include padding10px;
            width: 80%;
            height: 100%;
            background-color: $peacefulgrey;
            .actualImage {
                @include centerBG;
                width: 100%;
                height: 100%;
                background-color: darkblue;
            }
        }
        .newsInfo {
            @include padding10px;
            @include columnFlex;
            gap: 10px;

            padding-top: 20px;
            width: 20%;
            height: 100%;
            background-color: $peacefulgrey;
            .newsAbout{
                text-align: justify;
            }
        }
    }
    .sideNews {
        @include padding10px;
        @include columnFlex;
        @include roundCorner;

        justify-content: space-between;
        height: 100%;
        width: 20%;
        background-color: $beerbrown;
    }
}
</style>